
.sidebar {
  position: fixed;
  top: 10px;
  left: 0;
  height: 100vh;
  width: var(--sidebar-w);
  background-color: #f2f2f254;
  backdrop-filter: blur(50px) !important;
  border-right: 1px solid var(--border-color);
  transition: width 0.3s ease, padding 0.3s ease, left 0.3s ease,
    right 0.3s ease;
  overflow: hidden;
  border-radius: 0 30px 30px 0;
  display: flex;
  flex-direction: column;
}

.sidebar.rtl {
  left: auto;
  right: 0px;
  border-right: none;
  border-left: 1px solid var(--border-color);
  border-radius: 30px 0px 0px 0px;
}

.sidebar:not(.rtl) {
  left: 0px;
  right: auto;
  border-right: 1px solid var(--border-color);
  border-radius: 0px 30px 0px 0px;
}

.sidebar.collapsed {
  width: var(--sidebar-w-collapsed);
}

.sidebar.collapsed .logo-section {
  opacity: 0;
  pointer-events: none;
  display: none;
}

.sidebar.collapsed .nav-text {
  opacity: 0;
  pointer-events: none;
  display: none;
}

.sidebar-header {
  padding: 1rem;
  border-bottom: 1px solid var(--border-color);
  display: flex;
  align-items: center;
  gap: 2.5rem;
}

.collapse-btn {
  background: none;
  border: 2px solid rgb(0, 0, 0, 0.12);
  color: #8b8b8b !important;
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 999rem;
  color: var(--text-secondary);
  transition: background-color 0.2s ease, color 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.collapse-btn:hover {
  background-color: #09365f;
  color: #ecf4e9 !important;
  color: var(--text-primary);
}

.mobile-expand-btn {
  background: none;
  border: 1px solid #158638;
  color: #158638 !important;
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 999rem;
  transition: background-color 0.2s ease, color 0.2s ease;
  display: none; /* Hidden by default, shown on mobile */
  align-items: center;
  justify-content: center;
}

.mobile-expand-btn:hover {
  background-color: #158638;
  color: #ecf4e9 !important;
}

.chevron-icon {
  transition: transform 0.3s ease;
}

.chevron-icon.rotated {
  transform: rotate(180deg);
}

.logo-section {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  opacity: 1;
  transition: opacity 0.3s ease;
}

.logo {
  width: 151px;
  height: 50px;
  object-fit: contain;
}

.logo-text {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  color: var(--brand-green);
  font-size: 1.25rem;
}

.sidebar-nav {
  padding: 1rem 0;
  height: calc(100vh - 270px); /* Reduced height to make room for bottom section */
  display: flex;
  background-color: #ffffff;
  flex-direction: column;
  justify-content: space-between;
  overflow-y: auto;
  overflow-x: hidden;
  /* flex: 1; */
  /* Hide scrollbar while keeping scroll functionality */
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* Internet Explorer 10+ */
}

.sidebar-nav::-webkit-scrollbar {
  display: none; /* WebKit browsers (Chrome, Safari, Edge) */
}

.nav-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.nav-item {
  margin: 0.25rem 0;
}

.nav-link {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.75rem 1rem;
  color: #333333be;
  text-decoration: none;
  border-radius: 0.375rem;
  font-size: 16px !important;
  margin: 0 0.5rem;
  font-weight: 600;
  transition: background-color 0.2s ease, color 0.2s ease, box-shadow 0.2s ease;
  position: relative;
}

.nav-link:hover {
  background-color: rgba(85, 85, 85, 0.05);
}

.nav-link.active {
  background: linear-gradient(#09365f 100%);
  color: white;
  font-size: 18px !important;

  box-shadow: var(--shadow-light);
}

.nav-link.active::before {
  content: "";
  position: absolute;
  inset-inline-start: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 4px;
  height: 20px;
  background-color: #ffffff;
  border-radius: 0 2px 2px 0;
}

.nav-link.rtl.active::before {
  inset-inline-end: 0;
  inset-inline-start: auto;
  border-radius: 2px 0 0 2px;
}

.nav-icon {
  flex-shrink: 0;
  color: currentColor;
}

.nav-text {
  font-family: inherit;
  font-size: 18px !important;
  font-weight: 600;
  white-space: nowrap;
  opacity: 1;
  transition: opacity 0.3s ease;
}

.logout-btn {
  background: transparent;
  border: none;
  width: 95%;
  text-align: start;
  cursor: pointer;
  transition: all 0.3s ease;
}

.logout-btn:hover {
  background: rgba(239, 68, 68, 0.1) !important;
  color: #ef4444 !important;
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(239, 68, 68, 0.2);
}

.logout-btn:active {
  transform: translateY(0);
  box-shadow: 0 2px 8px rgba(239, 68, 68, 0.3);
}

.nav-divider {
  height: 1px;
  background-color: var(--border-color);
  margin: 1rem 0.5rem;
}

/* Tooltip for collapsed state */
.sidebar.collapsed .nav-link {
  position: relative;
}

.sidebar.collapsed .nav-link::after {
  content: attr(data-tooltip);
  position: absolute;
  inset-inline-start: 100%;
  top: 50%;
  transform: translateY(-50%);
  background-color: var(--text-primary);
  color: white;
  padding: 0.5rem;
  border-radius: 0.375rem;
  font-size: 0.75rem;
  white-space: nowrap;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.2s ease;
  z-index: 1001;
  margin-inline-start: 0.5rem;
}

.sidebar.collapsed .nav-link:hover::after {
  opacity: 1;
}

.sidebar.rtl.collapsed .nav-link::after {
  inset-inline-end: 100%;
  inset-inline-start: auto;
  margin-inline-end: 0.5rem;
  margin-inline-start: 0;
}

/* Responsive adjustments */
/* Small screens - mobile behavior */
@media (max-width: 768px) {
  .sidebar {
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1;
    transition: all 0.3s ease;
  }

  .sidebar.expanded {
    width: 250px;
    z-index: 1000 !important;
  }
  .users-page {
    z-index: 999 !important;
  }

  /* Ensure sidebar stays below modals */
  .sidebar {
    z-index: 1 !important;
  }

  .sidebar:not(.expanded) {
    width: 60px;
  }

  .sidebar:not(.expanded) .logo-section,
  .sidebar:not(.expanded) .nav-text {
    opacity: 0;
    pointer-events: none;
    display: none;
  }

  .sidebar:not(.expanded) .nav-link {
    justify-content: center;
    padding: 1rem 0.5rem;
  }

  .mobile-expand-btn {
    display: block;
  }

  .collapse-btn {
    display: none;
  }
}

/* Large screen behavior - restore old collapsed behavior */
@media (min-width: 769px) {
  .sidebar.collapsed {
    width: var(--sidebar-w-collapsed);
  }

  .sidebar.collapsed .logo-section {
    opacity: 0;
    pointer-events: none;
    display: none;
  }

  .sidebar.collapsed .nav-text {
    opacity: 0;
    pointer-events: none;
    display: none;
  }

  .sidebar.collapsed .nav-link {
    justify-content: center;
    padding: 1rem 0.5rem;
  }

  .sidebar.collapsed .nav-link svg {
    margin: 0;
  }

  /* Hide mobile expand button on large screens */
  .mobile-expand-btn {
    display: none;
  }

  /* Show desktop collapse button on large screens */
  .collapse-btn {
    display: flex;
  }
}

/* User Profile Section */
.user-profile-section {
  color: #09365f;
  margin: 0 0.5rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: all 0.3s ease;
  cursor: pointer;
  width: 95%;
}

.user-profile-info:hover {
  background: #09365f !important;
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(255, 255, 255, 0.3);
}

.user-profile-info:hover .user-name {
  color: #ffffff !important;
}

.user-profile-section:hover .user-role {
  color: #666666 !important;
}

.user-profile-info {
  border: 1px solid #080264 !important;
  border-radius: 12px;
  padding: 0.5rem;
  display: flex;
  align-items: center;
  gap: 0.75rem;
  transition: all 0.3s ease;
  width: 100%;
}

.user-avatar {
  flex-shrink: 0;
}

.user-avatar .profile-pic {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid rgba(255, 255, 255, 0.3);
}

.user-avatar svg {
  width: 40px;
  height: 40px;
  color: #8b8b8b;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  padding: 8px;
  background: rgba(255, 255, 255, 0.1);
}

.user-details {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  min-width: 0;
}

.user-name {
  font-weight: 600;
  color: #333333;
  font-size: 14px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.user-role {
  font-size: 12px;
  color: #8b8b8b;
  font-weight: 500;
}

/* Logout Button Styling */
.logout-btn {
  background: #ffdcd4 !important;
  color: #ff7253 !important;
  /* border: 2px solid #ef4444 !important; */
  border-radius: 8px !important;
  margin: 0 0.5rem !important;
  padding: 0.75rem 1rem !important;
  font-weight: 600 !important;
  transition: all 0.3s ease !important;
  margin-bottom: 1rem !important;
}

.logout-btn:hover {
  background: #dc2626 !important;
  border-color: #dc2626 !important;
  color: white !important;
  transform: translateY(-2px) !important;
  box-shadow: 0 8px 25px rgba(239, 68, 68, 0.4) !important;
}

.logout-btn:active {
  transform: translateY(0) !important;
  box-shadow: 0 4px 15px rgba(239, 68, 68, 0.3) !important;
}

/* Fixed Bottom Section */
.sidebar-bottom {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.1);
  border-top: 1px solid var(--border-color);
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  z-index: 10;
}

.bottom-section {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

/* Notification Button Styling */
.notification-btn {
  background: #e0f2fe !important;
  color: #0277bd !important;
  border: 1px solid #81d4fa !important;
  border-radius: 12px !important;
  padding: 0.75rem 1rem !important;
  width: 100% !important;
  transition: all 0.3s ease !important;
  display: flex !important;
  align-items: center !important;
  gap: 0.5rem !important;
  justify-content: center !important;
}

.notification-btn:hover {
  background: #0277bd !important;
  color: white !important;
  transform: translateY(-2px) !important;
  box-shadow: 0 4px 15px rgba(2, 119, 189, 0.3) !important;
}

.notification-btn:active {
  transform: translateY(0) !important;
  box-shadow: 0 2px 8px rgba(2, 119, 189, 0.3) !important;
}

/* Responsive Design */
@media (max-width: 768px) {
  .sidebar-bottom {
    padding: 0.75rem;
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .bottom-section {
    width: 100%;
  }
  
  .notification-btn,
  .logout-btn {
    width: 100% !important;
    justify-content: center !important;
  }
  
  .user-profile-section {
    width: 100%;
  }
  
  .user-profile-info {
    justify-content: center !important;
  }
}

/* Collapsed Sidebar Styles */
.sidebar.collapsed .sidebar-bottom {
  padding: 0.5rem;
  flex-direction: column;
  gap: 0.5rem;
  align-items: center;
}

.sidebar.collapsed .bottom-section {
  width: 100%;
  justify-content: center;
}

.sidebar.collapsed .notification-btn,
.sidebar.collapsed .logout-btn {
  width: 100% !important;
  justify-content: center !important;
  padding: 0.75rem !important;
}

.sidebar.collapsed .user-profile-section {
  width: 100%;
}

.sidebar.collapsed .user-profile-info {
  justify-content: center !important;
  padding: 0.75rem !important;
}

/* Expanded Sidebar Styles */
.sidebar:not(.collapsed) .sidebar-bottom {
  flex-direction: column;
  gap: 0.75rem;
}

/* RTL Support */
.sidebar.rtl .sidebar-bottom {
  left: 0;
  right: 0;
}

/* Animation for smooth transitions */
.sidebar-bottom {
  transition: all 0.3s ease;
}
